import Assembler from "./Assembler";
import VMTranslator from "./VMTranslator";
import JackCompiler from "./JackCompiler";

/**
 * Assembles hack assembly code into hack machine code.
 *
 * @param asmString string of asm code
 * @returns string of hack machine code
 */
export function assembleHackASM(asmString: string): string {
  return new Assembler(asmString).assemble();
}

export type File = {
  filename: string;
  text: string;
};

/**
 * Translates vm code into assembly
 *
 * @param inputFiles an array of objects
 * @param inputFiles.filename the vm file name
 * @param inputFiles.text the vm file contents
 * @returns string of asm code that can be passed to the assembler
 */
export function translateVMFilesToASM(inputFiles: File[]): string {
  const asmString = VMTranslator.translateFiles(
    inputFiles.map((inFile) => {
      return {
        fileName: inFile.filename,
        readFile: () => inFile.text,
      };
    })
  );
  return asmString;
}

/**
 * Compiles jack files into vm files
 */
export function compileJackToVM(inputFiles: File[]): File[] {
  return inputFiles.map((inputFile) => {
    return {
      filename: inputFile.filename.replace(".jack", ".vm"),
      text: JackCompiler.compile(inputFile.text),
    };
  });
}

export function compileJackFilesToMachineCode(inputFiles: File[]): string {
  return assembleHackASM(translateVMFilesToASM(compileJackToVM(inputFiles)));
}
