import type { IdentifierToken, KeywordToken, Token } from "./JackTokenizer";
import JackTokenizer from "./JackTokenizer";
import SymbolTable, { SymbolTableItem } from "./VMSymbolTable";
import { Segment } from "./VMTranslator";
import VMWriter from "./VMWriter";

const SUBROUTINE_TYPES = ["constructor", "function", "method"] as const;
type SubroutineType = typeof SUBROUTINE_TYPES[number];
function isSubroutineType(s: string): s is SubroutineType {
  return (SUBROUTINE_TYPES as readonly string[]).includes(s);
}

const TYPES = ["int", "char", "boolean"] as const;
type DataType = typeof TYPES[number];
function isDataType(s: string): s is DataType {
  return (TYPES as readonly string[]).includes(s);
}

const OPERATORS = ["+", "-", "*", "/", "&", "|", "<", ">", "="] as const;
type Operator = typeof OPERATORS[number];
function isOperator(s: string): s is Operator {
  return (OPERATORS as readonly string[]).includes(s);
}

function isTypeToken(token: Token): token is IdentifierToken | KeywordToken {
  return (
    token.tokenType === "IDENTIFIER" ||
    (token.tokenType === "KEYWORD" && isDataType(token.keyWord))
  );
}

function getIdentifierType(typeToken: Token): string {
  if (!isTypeToken(typeToken)) {
    throw new Error(
      "Expected a valid type token but got " + typeToken.tokenType
    );
  }
  if (typeToken.tokenType === "KEYWORD") {
    return typeToken.keyWord;
  } else {
    return typeToken.identifier;
  }
}

function getSegmentForSymbol(symbol: SymbolTableItem): Segment {
  switch (symbol.kind) {
    case "arg":
      return "argument";
    case "var":
      return "local";
    case "field":
      return "this";
    case "static":
      return "static";
    default:
      throw new Error(
        `Not sure what segment to pop value onto in "let ${symbol.name}= ..." statement`
      );
  }
}

export default class JackCompiler {
  static compile(jackString: string): string {
    return new JackCompiler(jackString).compile();
  }
  output: string = "";
  tokenizer: JackTokenizer;
  table: SymbolTable;
  className: string = "";
  vmWriter: VMWriter;
  vmCode: string;
  constructor(jackString: string) {
    this.tokenizer = new JackTokenizer(jackString);
    this.table = new SymbolTable();
    this.vmCode = "";
    this.vmWriter = new VMWriter({
      writeLn: (s: string) => {
        this.log(s);
        this.vmCode += s + "\n";
      },
    });
  }

  private log(...s: any[]) {
    // console.log(...s);
  }

  tagIndent = "";
  private openTag(tag: string) {
    const s = `${this.tagIndent}<${tag}>`;
    this.log(s);
    this.output += s + "\n";
    this.tagIndent += "  ";
  }
  private closeTag(tag: string) {
    this.tagIndent = this.tagIndent.slice(0, this.tagIndent.length - 2);
    const s = `${this.tagIndent}</${tag}>`;
    this.log(s);
    this.output += s + "\n";
  }
  private tag(tag: string, val: string) {
    const s = `${this.tagIndent}<${tag}> ${val} </${tag}>`;
    this.log(s);
    this.output += s + "\n";
  }
  private tagIdenfitier(
    name: string,
    category:
      | "var"
      | "arg"
      | "static"
      | "field"
      | "class"
      | "subroutine"
      | "none",
    status: "defined" | "used",
    kind: "var" | "arg" | "static" | "field" | "none",
    type: string,
    index: number
  ) {
    const s = `${this.tagIndent}<id category="${category}" status="${status}" kind="${kind}" type="${type}" index="${index}"> ${name} </id>`;
    this.log(s);
    this.output += s + "\n";
  }

  private tagToken(token: Token) {
    if (token.tokenType === "IDENTIFIER") {
      this.tag("identifier", token.identifier);
    } else if (token.tokenType === "INT_CONST") {
      this.tag("integerConstant", "" + token.intVal);
    } else if (token.tokenType === "KEYWORD") {
      this.tag("keyword", token.keyWord);
    } else if (token.tokenType === "STRING_CONST") {
      this.tag("stringConstant", token.stringVal);
    } else if (token.tokenType === "SYMBOL") {
      let symbol = token.symbol;
      if (symbol === "<") {
        symbol = "&lt;";
      } else if (symbol === ">") {
        symbol = "&gt;";
      } else if (symbol === "&") {
        symbol = "&amp;";
      }
      this.tag("symbol", symbol);
    }
  }

  private compileType() {
    const typeToken = this.tokenizer.getToken();
    if (isTypeToken(typeToken)) {
      this.tagToken(typeToken);
      this.tokenizer.advance();
    } else {
      throw new Error(`Expected a valid type.`);
    }
    return typeToken;
  }

  private compileClassVarDec() {
    this.openTag("classVarDec");

    const varTypeToken = this.tokenizer.getKeywordToken();
    const kind = varTypeToken.keyWord;
    if (kind !== "static" && kind !== "field") {
      throw new Error(
        "Class variable declaration must begin with static or field keyword"
      );
    }
    this.tagToken(varTypeToken);
    this.tokenizer.advance();

    const dataTypeToken = this.tokenizer.getToken();
    this.tokenizer.advance();

    let identifierToken = this.tokenizer.getToken();
    if (identifierToken.tokenType !== "IDENTIFIER") {
      throw new Error("Expected identifier in class variable declaration");
    }
    this.tokenizer.advance();

    let symbol = this.table.addIdentifier(
      identifierToken.identifier,
      getIdentifierType(dataTypeToken),
      kind
    );
    this.tagIdenfitier(
      symbol.name,
      symbol.kind,
      "defined",
      symbol.kind,
      symbol.type,
      symbol.index
    );

    let nextToken = this.tokenizer.getToken();
    while (nextToken.tokenType === "SYMBOL" && nextToken.symbol === ",") {
      this.tagToken(nextToken);
      this.tokenizer.advance();

      let identifierToken = this.tokenizer.getToken();
      if (identifierToken.tokenType !== "IDENTIFIER") {
        throw new Error("Expected identifier in class variable declaration");
      }
      this.tokenizer.advance();
      let symbol = this.table.addIdentifier(
        identifierToken.identifier,
        getIdentifierType(dataTypeToken),
        kind
      );
      this.tagIdenfitier(
        symbol.name,
        symbol.kind,
        "defined",
        symbol.kind,
        symbol.type,
        symbol.index
      );
      nextToken = this.tokenizer.getToken();
    }
    if (nextToken.tokenType !== "SYMBOL" || nextToken.symbol !== ";") {
      throw new Error("Expected ;");
    }
    this.tagToken(nextToken);
    this.tokenizer.advance();

    this.closeTag("classVarDec");
  }

  private compileParameterList() {
    this.openTag("parameterList");

    let nextToken = this.tokenizer.getToken();
    while (isTypeToken(nextToken)) {
      this.compileType();
      const varNameToken = this.tokenizer.getToken();
      if (varNameToken.tokenType !== "IDENTIFIER") {
        throw new Error("Expected a variable name token in paramter list");
      }
      this.table.addIdentifier(
        varNameToken.identifier,
        getIdentifierType(nextToken),
        "arg"
      );
      this.tagToken(varNameToken);
      const commaToken = this.tokenizer.advance();
      if (commaToken.tokenType === "SYMBOL" && commaToken.symbol === ",") {
        this.tagToken(commaToken);
        this.tokenizer.advance();
      }
      nextToken = this.tokenizer.getToken();
    }

    this.closeTag("parameterList");
  }

  private compileVarDec() {
    this.openTag("varDec");

    const kind = this.tokenizer.getKeywordToken().keyWord;
    if (kind !== "var") {
      throw new Error("variable declarations must begin with var keyword");
    }
    this.tokenizer.advance();

    const typeToken = this.tokenizer.getToken();
    this.tokenizer.advance();

    let numVars = 0;
    const varNameToken = this.tokenizer.getToken();
    if (varNameToken.tokenType !== "IDENTIFIER") {
      throw new Error("Expected variable name after variable type");
    }
    numVars++;
    this.tokenizer.advance();
    let symbol = this.table.addIdentifier(
      varNameToken.identifier,
      getIdentifierType(typeToken),
      kind
    );
    this.tagIdenfitier(
      symbol.name,
      symbol.kind,
      "defined",
      symbol.kind,
      symbol.type,
      symbol.index
    );

    let nextToken = this.tokenizer.getToken();
    while (nextToken.tokenType === "SYMBOL" && nextToken.symbol === ",") {
      this.tagToken(nextToken);
      const nextVarNameToken = this.tokenizer.advance();
      if (nextVarNameToken.tokenType !== "IDENTIFIER") {
        throw new Error(
          "comma in variable declaration must be followed by identifier"
        );
      }
      numVars++;
      this.tokenizer.advance();
      let symbol = this.table.addIdentifier(
        nextVarNameToken.identifier,
        getIdentifierType(typeToken),
        kind
      );
      this.tagIdenfitier(
        symbol.name,
        symbol.kind,
        "defined",
        symbol.kind,
        symbol.type,
        symbol.index
      );
      nextToken = this.tokenizer.getToken();
    }
    if (nextToken.tokenType !== "SYMBOL" || nextToken.symbol !== ";") {
      throw new Error("Variable declaration statement must end with ;");
    }
    this.tagToken(nextToken);
    this.tokenizer.advance();

    this.closeTag("varDec");
    return numVars;
  }

  private isStatement(token: Token): token is KeywordToken {
    return (
      token.tokenType === "KEYWORD" &&
      ["let", "if", "while", "do", "return"].includes(token.keyWord)
    );
  }

  /**
   * deal with a list of comma-separated expressions as in the case of a function call:
   *   do MyClass.someFunc(a, 3+4, true);
   *                       ^^^^^^^^^^^^
   * returns the number of items in the expression list
   */
  private compileExpressionList(): number {
    this.openTag("expressionList");
    let numItems = 0;
    let nextToken = this.tokenizer.getToken();
    while (nextToken.tokenType !== "SYMBOL" || nextToken.symbol !== ")") {
      this.compileExpression();
      numItems++;
      nextToken = this.tokenizer.getToken();
      if (nextToken.tokenType === "SYMBOL" && nextToken.symbol === ",") {
        this.tagToken(nextToken);
        nextToken = this.tokenizer.advance();
      }
    }
    this.closeTag("expressionList");
    return numItems;
  }

  private compileSubroutineCall(nameToken: IdentifierToken) {
    this.tagToken(nameToken);
    let nextToken = this.tokenizer.getSymbolToken();
    let vmFuncName = `${this.className}.${nameToken.identifier}`;
    let vmFuncArgCount = 0;
    if (nextToken.symbol === "(") {
      // calling a method within a class, like setFoo(3);
      this.vmWriter.writePush("pointer", 0);
      vmFuncArgCount += 1;

      this.tagToken(nextToken);
      this.tokenizer.advance();
    } else if (nextToken.symbol === ".") {
      // calling a function on another class like Math.sqrt(4);
      this.tagToken(nextToken);
      this.tokenizer.advance();
      const subroutineNameToken = this.tokenizer.getIdentifierToken();
      this.tagToken(subroutineNameToken);
      this.tokenizer.advance();

      vmFuncName = `${nameToken.identifier}.${subroutineNameToken.identifier}`;

      if (this.table.has(nameToken.identifier)) {
        // nameToken is referencing a local/instance/class variable like:
        //   let game = Game.new();
        //   game.doSomething();
        const nameSymbol = this.table.get(nameToken.identifier);
        vmFuncName = `${nameSymbol.type}.${subroutineNameToken.identifier}`;
        switch (nameSymbol.kind) {
          case "arg":
            this.vmWriter.writePush("argument", nameSymbol.index);
            break;
          case "var":
            this.vmWriter.writePush("local", nameSymbol.index);
            break;
          case "field":
            this.vmWriter.writePush("this", nameSymbol.index);
            break;
          // TODO: handle the rest of these
          case "static":
            this.vmWriter.writePush("static", nameSymbol.index);
            break;
          case "none":
          default:
            throw new Error(
              `Not sure how to handle calling methods on ${nameSymbol.kind}`
            );
        }
        vmFuncArgCount += 1; // we need to pass the object.
      }

      const openParen = this.tokenizer.getSymbolToken();
      if (openParen.symbol !== "(") {
        throw new Error("Expected ( in class function call");
      }
      this.tagToken(openParen);
      this.tokenizer.advance();
    } else {
      throw new Error(
        "expected ( or . following subroutine name in subroutine call"
      );
    }

    vmFuncArgCount += this.compileExpressionList();

    nextToken = this.tokenizer.getSymbolToken();
    if (nextToken.symbol !== ")") {
      throw new Error("Expected closing ) in subroutine call");
    }

    this.tagToken(nextToken);
    this.tokenizer.advance();
    this.vmWriter.writeCall(vmFuncName, vmFuncArgCount);
  }

  private compileTerm() {
    this.openTag("term");
    let nextToken = this.tokenizer.getToken();
    if (nextToken.tokenType === "INT_CONST") {
      this.vmWriter.writePush("constant", nextToken.intVal);
      this.tagToken(nextToken);
      this.tokenizer.advance();
    } else if (nextToken.tokenType === "STRING_CONST") {
      // Let's construct a string constant!
      // Step 1: call String.new, passing in the string length
      const stringVal = nextToken.stringVal;
      this.vmWriter.writePush("constant", stringVal.length);
      this.vmWriter.writeCall("String.new", 1);
      // Step 2: now the memory address of the string is at the top of the stack
      // and we want to append the each character to it with String.appendChar
      for (let i = 0; i < stringVal.length; i++) {
        this.vmWriter.writePush("constant", stringVal.charCodeAt(i));
        this.vmWriter.writeCall("String.appendChar", 2);
      }
      // Step 3: TODO: dispose of the string's memory when it is no longer needed
      this.tagToken(nextToken);
      this.tokenizer.advance();
    } else if (
      nextToken.tokenType === "KEYWORD" &&
      ["true", "false", "null", "this"].includes(nextToken.keyWord)
    ) {
      switch (nextToken.keyWord) {
        case "true":
          this.vmWriter.writePush("constant", 1);
          this.vmWriter.writeArithmetic("neg");
          break;
        case "this":
          this.vmWriter.writePush("pointer", 0);
          break;
        case "false":
        case "null":
          this.vmWriter.writePush("constant", 0);
          break;
      }
      this.tagToken(nextToken);
      this.tokenizer.advance();
    } else if (nextToken.tokenType === "IDENTIFIER") {
      const varNameToken: IdentifierToken = nextToken;
      nextToken = this.tokenizer.advance();

      if (nextToken.tokenType === "SYMBOL" && nextToken.symbol === "[") {
        this.tagToken(varNameToken);
        this.tagToken(nextToken);
        this.tokenizer.advance();

        const symbol = this.table.get(varNameToken.identifier);
        this.vmWriter.writePush(getSegmentForSymbol(symbol), symbol.index);
        this.compileExpression();
        this.vmWriter.writeArithmetic("add");
        this.vmWriter.writePop("pointer", 1);
        this.vmWriter.writePush("that", 0);

        nextToken = this.tokenizer.getSymbolToken();
        if (nextToken.symbol !== "]") {
          throw new Error("Expected closing ] in array indexing term");
        }
        this.tagToken(nextToken);
        this.tokenizer.advance();
      } else if (
        nextToken.tokenType === "SYMBOL" &&
        ".(".includes(nextToken.symbol)
      ) {
        // this is term is a subroutine call like Math.sqrt() or doSomething()
        this.compileSubroutineCall(varNameToken);
      } else {
        // this term is a variable/argument/field reference
        const symbol = this.table.get(varNameToken.identifier);
        this.vmWriter.writePush(getSegmentForSymbol(symbol), symbol.index);
        this.tagToken(varNameToken);
      }
    } else if (nextToken.tokenType === "SYMBOL" && nextToken.symbol === "(") {
      this.tagToken(nextToken);
      this.tokenizer.advance();
      this.compileExpression();
      const closeParenToken = this.tokenizer.getSymbolToken();
      if (closeParenToken.symbol !== ")") {
        throw new Error("Expected closing ) in epxression");
      }
      this.tagToken(closeParenToken);
      this.tokenizer.advance();
    } else if (
      nextToken.tokenType === "SYMBOL" &&
      (nextToken.symbol === "-" || nextToken.symbol === "~")
    ) {
      this.tagToken(nextToken);
      this.tokenizer.advance();
      this.compileTerm();
      const opToCommand = { "-": "neg", "~": "not" } as const;
      this.vmWriter.writeArithmetic(opToCommand[nextToken.symbol]);
    }
    this.closeTag("term");
  }

  private compileExpression() {
    this.openTag("expression");

    this.compileTerm();

    let opToken = this.tokenizer.getToken();
    while (opToken.tokenType === "SYMBOL" && isOperator(opToken.symbol)) {
      this.tagToken(opToken);
      this.tokenizer.advance();
      this.compileTerm();

      const op = opToken.symbol;
      if (op === "*") {
        this.vmWriter.writeCall("Math.multiply", 2);
      } else if (op === "/") {
        this.vmWriter.writeCall("Math.divide", 2);
      } else {
        const opToCommand = {
          "+": "add",
          "-": "sub",
          "&": "and",
          "|": "or",
          "<": "lt",
          ">": "gt",
          "=": "eq",
        } as const;
        this.vmWriter.writeArithmetic(opToCommand[op]);
      }
      opToken = this.tokenizer.getToken();
    }

    this.closeTag("expression");
  }

  /**
   * let foo = 1+3;
   */
  private compileLetStatement() {
    this.openTag("letStatement");
    const letToken = this.tokenizer.getKeywordToken();
    if (letToken.keyWord !== "let") {
      throw new Error("expected let statement to begin with let keyword");
    }
    this.tagToken(letToken);
    this.tokenizer.advance();
    const varNameToken = this.tokenizer.getIdentifierToken();
    const symbol = this.table.get(varNameToken.identifier);
    this.tagToken(varNameToken);

    let nextToken = this.tokenizer.advance();
    let isArrayAssignment = false;
    if (nextToken.tokenType === "SYMBOL" && nextToken.symbol === "[") {
      // handle array assignment
      // let a[i] = 3;
      isArrayAssignment = true;
      this.tagToken(nextToken);
      this.tokenizer.advance();

      this.vmWriter.writePush(getSegmentForSymbol(symbol), symbol.index);

      this.compileExpression();

      this.vmWriter.writeArithmetic("add");
      // now the memory address of the array element we want to write to
      // is at the top of the stack, so let's save it temporarily
      this.vmWriter.writePop("temp", 0);

      const closeBracketToken = this.tokenizer.getSymbolToken();
      if (closeBracketToken.symbol !== "]") {
        throw new Error("Expected closing ] in array indexed let statement");
      }
      this.tagToken(closeBracketToken);
      nextToken = this.tokenizer.advance();
    }

    if (nextToken.tokenType !== "SYMBOL" || nextToken.symbol !== "=") {
      throw new Error("Expected = operator in let statement");
    }
    this.tagToken(nextToken);
    this.tokenizer.advance();

    this.compileExpression();

    const semiColonToken = this.tokenizer.getSymbolToken();
    if (semiColonToken.symbol !== ";") {
      throw new Error("expected ; at end of let statement");
    }
    this.tagToken(semiColonToken);
    this.tokenizer.advance();

    this.closeTag("letStatement");

    if (isArrayAssignment) {
      // restore the memory address of the array element to the top of the stack from the temp segment
      this.vmWriter.writePush("temp", 0);
      // now make "that" point to this address.
      this.vmWriter.writePop("pointer", 1);
      this.vmWriter.writePop("that", 0);
    } else {
      this.vmWriter.writePop(getSegmentForSymbol(symbol), symbol.index);
    }
  }

  private compileDoStatement() {
    this.openTag("doStatement");
    this.tagToken(this.tokenizer.getToken());
    this.tokenizer.advance();
    const nameToken = this.tokenizer.getIdentifierToken();
    this.tokenizer.advance();
    this.compileSubroutineCall(nameToken);
    this.tagToken(this.tokenizer.getToken()); // ending semicolon
    this.tokenizer.advance();
    this.closeTag("doStatement");
    this.vmWriter.writePop("temp", 0); // TODO: is it right to throw away the return value of the subroutine call? And to throw it away onto the temp segment?
  }

  /**
   * Compiles a return statement.
   * Example 1: return statement with expression
   *
   *     return 3+4;
   *
   *   This statement will push 7 onto the stack and then return to the caller
   *
   * Example 2: return statement for void method (no expression)
   *
   *     return;
   *
   *   This statement will push 0 onto the stack and then return to the caller
   *
   */
  private compileReturn() {
    this.openTag("returnStatement");
    this.tagToken(this.tokenizer.getToken()); // 'return'
    const nextToken = this.tokenizer.advance();
    if (nextToken.tokenType !== "SYMBOL" || nextToken.symbol !== ";") {
      this.compileExpression();
    } else {
      this.vmWriter.writePush("constant", 0);
    }
    this.tagToken(this.tokenizer.getToken()); // ;
    this.tokenizer.advance();
    this.closeTag("returnStatement");
    this.vmWriter.writeReturn();
  }

  ifStatementCount = 0;
  private compileIf() {
    const ifId = this.ifStatementCount++;
    this.openTag("ifStatement");

    this.tagToken(this.tokenizer.getToken()); // 'if'
    this.tokenizer.advance();

    this.tagToken(this.tokenizer.getToken()); // '('
    this.tokenizer.advance();

    this.compileExpression();

    this.vmWriter.writeIf(`IF_TRUE_${ifId}`);
    this.vmWriter.writeGoto(`IF_FALSE_${ifId}`);
    this.vmWriter.writeLabel(`IF_TRUE_${ifId}`);

    this.tagToken(this.tokenizer.getToken()); // ')'
    this.tokenizer.advance();

    this.tagToken(this.tokenizer.getToken()); // '{'
    this.tokenizer.advance();

    this.compileStatements();
    this.tagToken(this.tokenizer.getToken()); // '}'
    this.tokenizer.advance();

    let elseToken = this.tokenizer.getToken();
    if (elseToken.tokenType === "KEYWORD" && elseToken.keyWord === "else") {
      this.tagToken(elseToken); // 'else'
      this.tokenizer.advance();

      this.tagToken(this.tokenizer.getToken()); // '{'
      this.tokenizer.advance();
      // if we didn't already goto IF_FALSE, then it means
      // we are at the end of IF_TRUE and need to skip the
      // else block by going to IF_END.
      this.vmWriter.writeGoto(`IF_END_${ifId}`);

      this.vmWriter.writeLabel(`IF_FALSE_${ifId}`);
      this.compileStatements();
      this.vmWriter.writeLabel(`IF_END_${ifId}`);

      this.tagToken(this.tokenizer.getToken()); // '}'
      this.tokenizer.advance();
    } else {
      // if there isn't an else block at all, then IF_FALSE
      // comes immediately after the statements in the if block.
      this.vmWriter.writeLabel(`IF_FALSE_${ifId}`);
    }
    this.closeTag("ifStatement");
  }

  whileLoopCount = 0;
  private compileWhile() {
    const loopId = this.whileLoopCount++;
    this.openTag("whileStatement");
    this.tagToken(this.tokenizer.getToken()); // 'while'
    this.tokenizer.advance();

    this.tagToken(this.tokenizer.getToken()); // '('
    this.tokenizer.advance();

    this.vmWriter.writeLabel(`WHILE_${loopId}`);

    this.compileExpression();
    this.vmWriter.writeArithmetic("not");
    this.vmWriter.writeIf(`WHILE_END_${loopId}`);

    this.tagToken(this.tokenizer.getToken()); // ')'
    this.tokenizer.advance();

    this.tagToken(this.tokenizer.getToken()); // '{'
    this.tokenizer.advance();

    this.compileStatements();

    this.vmWriter.writeGoto(`WHILE_${loopId}`);
    this.vmWriter.writeLabel(`WHILE_END_${loopId}`);

    this.tagToken(this.tokenizer.getToken()); // '}'
    this.tokenizer.advance();

    this.closeTag("whileStatement");
  }

  private compileStatement() {
    const statementType = this.tokenizer.getKeywordToken().keyWord;
    if (statementType === "let") {
      this.compileLetStatement();
    } else if (statementType === "do") {
      this.compileDoStatement();
    } else if (statementType === "return") {
      this.compileReturn();
    } else if (statementType === "if") {
      this.compileIf();
    } else if (statementType === "while") {
      this.compileWhile();
    } else {
      throw new Error("Unknown statement type: " + statementType);
    }
  }

  private compileStatements() {
    this.openTag("statements");

    let nextToken = this.tokenizer.getToken();
    while (this.isStatement(nextToken)) {
      this.compileStatement();
      nextToken = this.tokenizer.getToken();
    }

    this.closeTag("statements");
  }

  private compileSubroutineBody() {
    this.compileStatements();

    const closeCurlyToken = this.tokenizer.getSymbolToken();
    if (closeCurlyToken.symbol !== "}") {
      throw new Error("Expected } symbol at end of subroutine body");
    }
    this.tagToken(closeCurlyToken);
    this.tokenizer.advance();

    this.closeTag("subroutineBody");
  }

  private compileSubroutineDec() {
    this.table.startSubroutine();
    this.openTag("subroutineDec");

    const subroutineTypeToken = this.tokenizer.getKeywordToken();
    if (!isSubroutineType(subroutineTypeToken.keyWord)) {
      throw new Error(
        "subroutines must start with constructor, function, or method keyword"
      );
    }
    this.tagToken(subroutineTypeToken);

    if (subroutineTypeToken.keyWord === "method") {
      // the first argument to a method is implicitly "this";
      this.table.addIdentifier("this", this.className, "arg");
    }

    this.tokenizer.advance();
    const returnTypeToken = this.tokenizer.getToken();
    if (
      returnTypeToken.tokenType === "KEYWORD" &&
      returnTypeToken.keyWord === "void"
    ) {
      this.tagToken(returnTypeToken);
      this.tokenizer.advance();
    } else {
      this.compileType();
    }

    const subroutineNameToken = this.tokenizer.getIdentifierToken();
    const subroutineName = subroutineNameToken.identifier;
    this.tagIdenfitier(
      subroutineNameToken.identifier,
      "subroutine",
      "defined",
      "none",
      (returnTypeToken.tokenType === "KEYWORD" && returnTypeToken.keyWord) ||
        (returnTypeToken.tokenType === "IDENTIFIER" &&
          returnTypeToken.identifier) ||
        "UNKNOWN",
      0
    );
    this.tagToken(subroutineNameToken);

    this.tokenizer.advance();
    const openParenToken = this.tokenizer.getSymbolToken();
    if (openParenToken.symbol !== "(") {
      throw new Error("Expected ( token after subroutine name");
    }
    this.tagToken(openParenToken);
    this.tokenizer.advance();

    this.compileParameterList();

    const closeParenToken = this.tokenizer.getSymbolToken();
    if (closeParenToken.symbol !== ")") {
      throw new Error("Expected ) token after parameter list");
    }
    this.tagToken(closeParenToken);
    this.tokenizer.advance();

    this.openTag("subroutineBody");

    const openCurlyToken = this.tokenizer.getSymbolToken();
    if (openCurlyToken.symbol !== "{") {
      throw new Error("Expected { symbol at beginning of subroutine body");
    }
    this.tagToken(openCurlyToken);

    let nextToken = this.tokenizer.advance();

    let numLocals = 0;
    while (nextToken.tokenType === "KEYWORD" && nextToken.keyWord === "var") {
      numLocals += this.compileVarDec();
      nextToken = this.tokenizer.getToken();
    }
    if (subroutineTypeToken.keyWord === "function") {
      this.vmWriter.writeFunction(
        `${this.className}.${subroutineName}`,
        numLocals
      );
      this.vmWriter.indent();
    } else if (subroutineTypeToken.keyWord === "constructor") {
      // When executing a constructor, we need to allocate space for the
      // fields of the instance with Memory.alloc and store the address
      // returned by Memory.alloc into the pointer segment
      this.vmWriter.writeFunction(
        `${this.className}.${subroutineName}`,
        numLocals
      );
      this.vmWriter.indent();
      this.vmWriter.writeComment(`begin constructor setup`);
      this.vmWriter.writePush("constant", this.table.getNumFields());
      this.vmWriter.writeCall("Memory.alloc", 1);
      this.vmWriter.writePop("pointer", 0);
      this.vmWriter.writeComment(`end constructor setup`);
    } else if (subroutineTypeToken.keyWord === "method") {
      // When execiting a method, the first argument is the memory address for
      // the current instance, which we load into the pointer segment so that
      // field lookups work.
      this.vmWriter.writeFunction(
        `${this.className}.${subroutineName}`,
        numLocals
      );
      this.vmWriter.indent();
      this.vmWriter.writeComment(`begin method setup`);
      this.vmWriter.writePush("argument", 0);
      this.vmWriter.writePop("pointer", 0);
      this.vmWriter.writeComment(`end method setup`);
    } else {
      throw new Error(
        "Not sure how to handle subroutine type " + subroutineTypeToken.keyWord
      );
    }

    this.compileSubroutineBody();
    this.vmWriter.dedent();

    this.closeTag("subroutineDec");
  }

  private compileClass() {
    this.openTag("class");

    const classToken = this.tokenizer.getKeywordToken();
    if (classToken.keyWord !== "class") {
      throw new Error("Expected class keyword");
    }
    this.tagToken(classToken);

    const classNameToken = this.tokenizer.advance();
    if (classNameToken.tokenType !== "IDENTIFIER") {
      throw new Error("Expected identifier token after class keyword");
    }
    this.className = classNameToken.identifier;
    this.tagIdenfitier(
      classNameToken.identifier,
      "class",
      "defined",
      "none",
      "",
      0
    );

    this.tokenizer.advance();
    const openCurlyToken = this.tokenizer.getSymbolToken();
    if (openCurlyToken.symbol !== "{") {
      throw new Error("Expected { symbol after class name token");
    }
    this.tagToken(openCurlyToken);

    let nextToken = this.tokenizer.advance();
    while (
      nextToken.tokenType === "KEYWORD" &&
      ["static", "field"].includes(nextToken.keyWord)
    ) {
      this.compileClassVarDec();
      nextToken = this.tokenizer.getToken();
    }

    while (
      nextToken.tokenType === "KEYWORD" &&
      ["constructor", "function", "method"].includes(nextToken.keyWord)
    ) {
      this.compileSubroutineDec();
      nextToken = this.tokenizer.getToken();
    }

    this.tagToken(nextToken); // }
    this.tokenizer.advance();

    this.closeTag("class");
  }

  compile() {
    this.tokenizer.advance();
    this.compileClass();
    this.log(this.vmCode);
    return this.vmCode;
  }
}
