const KEYWORDS = [
  "class",
  "constructor",
  "function",
  "method",
  "field",
  "static",
  "var",
  "int",
  "char",
  "boolean",
  "void",
  "true",
  "false",
  "null",
  "this",
  "let",
  "do",
  "if",
  "else",
  "while",
  "return",
] as const;
type KeywordType = typeof KEYWORDS[number];
function isKeyword(s: string): s is KeywordType {
  return (KEYWORDS as readonly string[]).includes(s);
}

export type IdentifierToken = {
  tokenType: "IDENTIFIER";
  identifier: string;
};

export type IntToken = {
  tokenType: "INT_CONST";
  intVal: number;
};

export type KeywordToken = {
  tokenType: "KEYWORD";
  keyWord: string;
};

export type StringToken = {
  tokenType: "STRING_CONST";
  stringVal: string;
};

export type SymbolToken = {
  tokenType: "SYMBOL";
  symbol: string;
};

export type Token =
  | IdentifierToken
  | IntToken
  | KeywordToken
  | StringToken
  | SymbolToken;

export default class JackTokenizer {
  jackString: string;
  charIndex: number = 0;

  token?: Token;

  constructor(jackString: string) {
    this.jackString = jackString;
  }

  hasMoreTokens() {
    return this.charIndex < this.jackString.length;
  }

  private eatWhitespace() {
    // ignore whitespace
    let char = this.jackString[this.charIndex];
    while (/\s/.test(char)) {
      this.charIndex++;
      char = this.jackString[this.charIndex];
    }
  }

  private eatComment() {
    // ignore comments
    const char2 = this.jackString.slice(this.charIndex, this.charIndex + 2);
    if (char2 === "//") {
      while (this.jackString[this.charIndex] !== "\n") {
        this.charIndex++;
      }
      this.charIndex++;
    } else if (char2 === "/*") {
      while (
        this.jackString.slice(this.charIndex, this.charIndex + 2) !== "*/"
      ) {
        this.charIndex++;
      }
      this.charIndex += 2;
    }
  }

  private parseStringConstant() {
    this.token = { tokenType: "STRING_CONST", stringVal: "" };

    this.charIndex++;
    let char = this.jackString[this.charIndex];
    while (char !== '"') {
      this.token.stringVal += char;
      this.charIndex++;
      char = this.jackString[this.charIndex];
    }
    this.charIndex++;
  }

  private parseIntegerConstant() {
    let intString = "";
    while (/\d/.test(this.jackString[this.charIndex])) {
      intString += this.jackString[this.charIndex];
      this.charIndex++;
    }
    this.token = { tokenType: "INT_CONST", intVal: parseInt(intString) };
  }

  private parseKeywordOrIdentifier() {
    let token = "";
    while (
      this.hasMoreTokens() &&
      /[A-Za-z\d_]/.test(this.jackString[this.charIndex])
    ) {
      token += this.jackString[this.charIndex];
      this.charIndex++;
    }
    if (isKeyword(token)) {
      this.token = { tokenType: "KEYWORD", keyWord: token };
    } else {
      this.token = { tokenType: "IDENTIFIER", identifier: token };
    }
  }

  public getToken(): Token {
    if (!this.token) {
      throw new Error("No token found");
    }
    return this.token;
  }

  public getKeywordToken(): KeywordToken {
    const token = this.getToken();
    if (token.tokenType !== "KEYWORD") {
      throw new Error("Expected KEYWORD token but found " + token.tokenType);
    }
    return token;
  }

  public getSymbolToken(): SymbolToken {
    const token = this.getToken();
    if (token.tokenType !== "SYMBOL") {
      throw new Error("Expected SYMBOL token but found " + token.tokenType);
    }
    return token;
  }

  public getIdentifierToken(): IdentifierToken {
    const token = this.getToken();
    if (token.tokenType !== "IDENTIFIER") {
      throw new Error("Expected IDENTIFIER token but found " + token.tokenType);
    }
    return token;
  }

  public advance(): Token {
    this.token = undefined;

    this.eatWhitespace();
    while (
      ["//", "/*"].includes(
        this.jackString.slice(this.charIndex, this.charIndex + 2)
      )
    ) {
      this.eatComment();
      this.eatWhitespace();
    }
    const char = this.jackString[this.charIndex];
    if (char === '"') {
      this.parseStringConstant();
    } else if ("0123456789".includes(char)) {
      this.parseIntegerConstant();
    } else if ("{}()[].,;+-*/&|<>=~".includes(char)) {
      this.token = { tokenType: "SYMBOL", symbol: char };
      this.charIndex++;
    } else if (/[a-zA-Z_]/.test(char)) {
      this.parseKeywordOrIdentifier();
    }
    return this.getToken();
  }
}
