export const SEGMENTS = [
  "constant",
  "argument",
  "local",
  "static",
  "this",
  "that",
  "pointer",
  "temp",
] as const;
export type Segment = typeof SEGMENTS[number];
export function isSegment(s?: string): s is Segment {
  return s !== undefined && (SEGMENTS as readonly string[]).includes(s);
}

export const ARITHMETIC_COMMANDS = [
  "neg",
  "not",
  "add",
  "sub",
  "and",
  "or",
  "eq",
  "lt",
  "gt",
] as const;
export type Arithmetic = typeof ARITHMETIC_COMMANDS[number];
export function isArithmetic(s?: string): s is Arithmetic {
  return (
    s !== undefined && (ARITHMETIC_COMMANDS as readonly string[]).includes(s)
  );
}

export enum CType {
  ARITHMETIC,
  PUSH,
  POP,
  LABEL,
  GOTO,
  IF,
  FUNCTION,
  RETURN,
  CALL,
}

export function getVMCodeLines(vmCode: string) {
  let lines: string[][] = [];
  for (const line of vmCode.split("\n")) {
    let i = 0;
    let tokens = [];
    let token = "";
    while (i < line.length) {
      if (/\s/.test(line[i])) {
        if (token.length > 0) {
          tokens.push(token);
          token = "";
        }
        i++;
      } else if (line.slice(i, i + 2) === "//") {
        break;
      } else {
        token += line[i];
        i++;
      }
    }
    if (token.length > 0) {
      tokens.push(token);
    }
    if (tokens.length > 0) {
      lines.push(tokens);
    }
  }
  return lines;
}

const tokenToCType: Record<string, CType> = {
  add: CType.ARITHMETIC,
  sub: CType.ARITHMETIC,
  neg: CType.ARITHMETIC,
  eq: CType.ARITHMETIC,
  gt: CType.ARITHMETIC,
  lt: CType.ARITHMETIC,
  and: CType.ARITHMETIC,
  or: CType.ARITHMETIC,
  not: CType.ARITHMETIC,
  push: CType.PUSH,
  pop: CType.POP,
  label: CType.LABEL,
  "if-goto": CType.IF,
  goto: CType.GOTO,
  function: CType.FUNCTION,
  return: CType.RETURN,
  call: CType.CALL,
};

class VMParser {
  lineIndex = 0;
  error?: string;
  commandType?: CType;
  arg1?: string;
  arg2?: number;
  vmCode: string;
  vmCodeLines: string[][];

  constructor(vmCode: string) {
    this.vmCodeLines = getVMCodeLines(vmCode);
    this.vmCode = vmCode;
  }

  hasMoreCommands = () => {
    return !this.error && this.lineIndex < this.vmCodeLines.length;
  };

  advance = () => {
    const line = this.vmCodeLines[this.lineIndex];
    this.lineIndex++;

    this.commandType = tokenToCType[line[0]];

    if (this.commandType === CType.ARITHMETIC) {
      this.arg1 = line[0];
    } else {
      this.arg1 = line[1];
    }

    if (
      [CType.PUSH, CType.POP, CType.FUNCTION, CType.CALL].includes(
        this.commandType
      )
    ) {
      this.arg2 = parseInt(line[2]);
    } else {
      this.arg2 = undefined;
    }
  };
}

function formatASM(asmString: string) {
  let lines = [];
  for (const line of asmString.split("\n")) {
    for (let i = 0; i < line.length; i++) {
      if (line[i] !== " ") {
        lines.push(line.slice(i, line.length));
        break;
      }
    }
  }
  return lines.join("\n");
}

type VMFile = {
  fileName: string;
  readFile: () => string;
};

export default class VMTranslator {
  parser: VMParser;
  indentation: string = "";
  output: string = "";
  fileName: string;
  returnAddressIndex = 0;
  currentFunction: string = "";

  public static translateFiles(
    vmFiles: VMFile[],
    options?: { lineNumbers?: boolean }
  ) {
    options = options || {};
    let asmString = "";
    if (vmFiles.length > 1) {
      asmString += formatASM(`
        @261 // SP=256+5
        D=A
        @SP
        M=D

        @Sys.init
        0;JMP
      `);
      asmString += "\n";
    }
    let stats: Record<string, number> = {};
    const labels: Record<string, boolean> = {};
    for (const vmFile of vmFiles) {
      const vmCode = vmFile.readFile();
      const translator = new VMTranslator(
        vmCode,
        vmFile.fileName,
        labels,
        stats
      );
      asmString += translator.translate();
      // Object.keys(translator.stats).forEach((k) => {
      //   stats[k] = (stats[k] || 0) + translator.stats[k];
      // });
    }
    // console.log("Translated all files. Stats:");
    // Object.keys(stats)
    //   .sort((a, b) => stats[a] - stats[b])
    //   .forEach((k) => console.log("  ", k, stats[k]));

    if (options.lineNumbers) {
      const lines: string[] = [];
      let lineIndex = 0;
      for (const line of asmString.split("\n")) {
        if (
          line.length > 0 &&
          !line.match(/^\s*\/\//) &&
          !line.match(/^\s*\(/)
        ) {
          let lineToWrite = line.padEnd(50) + `// line: ${lineIndex++}`;
          lines.push(lineToWrite);
        } else {
          lines.push(line);
        }
      }
      asmString = lines.join("\n");
    }

    return asmString;
  }
  existingLabels: Record<string, boolean>;
  private stats: Record<string, number>;
  constructor(
    vmCode: string,
    fileName: string,
    labels: Record<string, boolean>,
    stats: Record<string, number>
  ) {
    this.parser = new VMParser(vmCode);
    this.fileName = fileName;
    this.existingLabels = labels;
    this.stats = stats;
  }

  indent = () => {
    this.indentation += "    ";
  };
  dedent = () => {
    if (this.indentation.length > 4) {
      this.indentation = this.indentation.slice(0, this.indentation.length - 4);
    } else {
      this.indentation = "";
    }
  };

  private count(name: string, num: number = 1) {
    this.stats[name] = (this.stats[name] || 0) + num;
  }

  writeLines = (lines: string[] | string, source: string) => {
    let totalCount = 0;
    if (typeof lines === "string") {
      lines = [lines];
    }
    for (const line of lines) {
      for (const subline of line.split("\n")) {
        if (subline.length > 0 && subline[0] !== "/") {
          this.count(source);
          totalCount++;
        }
        this.output += this.indentation + subline + "\n";
      }
    }
    if (totalCount === 110) {
      debugger;
    }
    this.stats[`[MAX] ${source}`] = Math.max(
      totalCount,
      this.stats[`[MAX] ${source}`] || 0
    );
  };

  comparatorIndex = 0;
  writeArithmetic = (command: Arithmetic) => {
    if (command === "neg" || command === "not") {
      this.writeLines(
        formatASM(`
          @SP
          A=M-1
          M=${{ neg: "-M", not: "!M" }[command]}
        `),
        `arithmetic: ${command}`
      );
      return;
    }

    if (
      command === "add" ||
      command === "sub" ||
      command === "and" ||
      command === "or"
    ) {
      this.writeLines(
        formatASM(`
          @SP // decrement stack pointer
          AM=M-1
          D=M
          A=A-1

          // do the add
          M=${{ add: "D+M", sub: "M-D", and: "D&M", or: "D|M" }[command]}
        `),
        `arithmetic: ${command}`
      );
      return;
    }

    const trueSymbol = `${this.currentFunction}:SET_TRUE_${this
      .comparatorIndex++}`;
    this.writeLines(
      formatASM(`
        @SP // decrement stack pointer
        AM=M-1
        D=M
        A=A-1

        D=D-M
        M=-1
        @${trueSymbol}
        D;${{ eq: `JEQ`, lt: `JGT`, gt: `JLT` }[command]}

        @SP // store result on stack
        A=M-1
        M=0
        (${trueSymbol}) // set D register to -1 (true)
      `),
      `arithmetic: ${command}`
    );
  };

  assertSegmentBoundary = (segment: Segment, index: number) => {
    // some segments are of a fixed size, which we enforce here.
    if (segment === "temp" && index > 7) {
      throw new Error(
        `index ${index} for temp segment is invalid. Must be between 0 and 7.`
      );
    } else if (segment === "pointer" && index > 1) {
      throw new Error(
        `index ${index} for pointer segment is invalid. Must be 0 or 1.`
      );
    }
  };

  /**
   * A subroutine is a labeled group of asm instructions
   * that can be jumped to from somewhere else. Once the
   * code for the subroutine is finished, it will jump back
   * to the location from which it was called, which should be
   * stored in R15. As a result, the subroutine code should
   * never modify the value of R15.
   *
   * Subroutine calls require 6 instructions, so it only makes
   * sense to create subroutines that have more than 6 instructions
   * in them. Otherwise you are better off just inlining.
   *
   * @param label asm label for the subroutine
   * @param code asm code to be executed
   */
  writeSubroutineDef = (label: string, code: string) => {
    if (this.existingLabels[label]) {
      throw new Error(
        `Failed writing subroutine ${label}. Label already exists.`
      );
    }
    if (code.indexOf("@R15") !== -1) {
      throw new Error(
        `Failed writing subroutine ${label}. Subroutine code can't touch R15`
      );
    }
    this.existingLabels[label] = true;
    this.writeLines(
      formatASM(`
        (${label}) // subroutine: def
        ${code}
        @R15
        A=M
        0;JMP // subroutine: end def ${label}
      `),
      "subroutine"
    );
  };

  subroutineReturnCount = 0;
  writeCallSubroutine = (label: string) => {
    const returnLabel = `${label}_return_${this.subroutineReturnCount++}`;
    this.writeLines(
      formatASM(`
        @${returnLabel} // subroutine: call
        D=A
        @R15
        M=D
        @${label}
        0;JMP
        (${returnLabel}) // subroutine: end call
      `),
      "subroutine"
    );
  };

  writePush = (segment: Segment, index: number) => {
    this.assertSegmentBoundary(segment, index);
    const segmentMap = {
      argument: "ARG",
      local: "LCL",
      this: "THIS",
      that: "THAT",
    };

    if (segment === "constant") {
      if (index <= 1) {
        this.writeLines(
          formatASM(`
            @SP // push: begin
            M=M+1
            A=M-1 // set stack pointer - 1 to constant value
            M=${index} // push: end
          `),
          "push: constant <= 1"
        );
        return;
      } else {
        this.writeLines(
          formatASM(`
            @${index} // push: begin
            D=A
            @SP // increment stack pointer
            M=M+1
            A=M-1 // set stack pointer - 1 to constant value
            M=D // push: end
          `),
          "push: constant > 1"
        );
        return;
      }
    }

    let lines = [];
    if (segment === "temp") {
      lines.push(`@${5 + index}`);
    } else if (segment === "pointer") {
      lines.push(`@${3 + index}`);
    } else if (segment === "static") {
      lines.push(`@${this.fileName}.${index}`);
    } else {
      // load segment base into D register
      lines.push(`@${segmentMap[segment]}`);
      if (index === 0) {
        lines.push(`A=M`);
      } else if (index === 1) {
        lines.push(`A=M+1`);
      } else {
        lines.push(
          formatASM(`
              D=M // calculate segment offset
              @${index}
              A=D+A
            `)
        );
      }
    }

    // load segment memory into D register
    lines.push(`D=M`);
    // follow stack pointer to push value of D register
    lines.push(
      formatASM(`
          @SP // increment stack pointer
          M=M+1
          A=M-1 // set stack pointer - 1 to value from D
          M=D
        `)
    );

    this.writeLines(lines, `push: ${segment}`);
  };

  writePop = (segment: Segment, index: number) => {
    this.assertSegmentBoundary(segment, index);
    const segmentMap = {
      argument: "ARG",
      local: "LCL",
      this: "THIS",
      that: "THAT",
    };

    let lines = [];

    if (segment === "temp") {
      lines.push(`@${5 + index}`);
      lines.push(`D=A`);
    } else if (segment === "pointer") {
      lines.push(`@${3 + index}`);
      lines.push(`D=A`);
    } else if (segment === "static") {
      lines.push(`@${this.fileName}.${index}`);
      lines.push(`D=A`);
    } else if (segment === "constant") {
      throw new Error("Invalid pop command: Can't pop to constant segment");
    } else {
      if (index <= 1) {
        lines.push(
          formatASM(`
            @SP // load top of stack into D register and decrement SP
            AM=M-1
            D=M
            @${segmentMap[segment]}
            A=${index === 0 ? "M" : "M+1"}
            M=D
          `)
        );
      } else {
        lines.push(
          formatASM(`
            @${segmentMap[segment]}
            D=M
            @${index} // load index into A register
            D=D+A // calculate segment base offset

            @R13 // store pop destination into R13
            M=D
            @SP // load top of stack into D register
            AM=M-1
            D=M
            @R13 // then...
            A=M
            M=D
          `)
        );
      }

      this.writeLines(lines, `pop: ${segment}`);
      return;
    }

    lines.push(
      formatASM(`
        @R13 // store pop destination into R13
        M=D
        @SP // load top of stack into D register
        AM=M-1
        D=M
        @R13 // then...
        A=M
        M=D
      `)
    );

    this.writeLines(lines, `pop: ${segment}`);
  };

  getLabelInASM = (label: string) => `${this.currentFunction}$${label}`;

  writeLabel = (label: string) => {
    this.writeLines(`(${this.getLabelInASM(label)})`, "label");
  };

  writeIf = (label: string) => {
    this.writeLines(
      formatASM(`
        @SP
        M=M-1
        A=M
        D=M
        @${this.getLabelInASM(label)}
        D;JNE
    `),
      "if"
    );
  };

  writeGoto = (label: string) => {
    this.writeLines(
      formatASM(`
        @${this.getLabelInASM(label)}
        0;JMP
      `),
      "goto"
    );
  };

  writeFunction = (funcName: string, numLocals: number) => {
    this.currentFunction = funcName;
    this.writeLines(`(${funcName})`, "func");

    if (numLocals > 0) {
      this.writeLines(
        formatASM(`
            @LCL
            A=M
            M=0
        `),
        "func"
      );
      for (let i = 1; i < numLocals; i++) {
        this.writeLines(
          formatASM(`
            A=A+1
            M=0
          `),
          "func"
        );
      }
      this.writeLines(
        formatASM(`
          @${numLocals}
          D=A
          @SP
          M=D+M
        `),
        "func"
      );
    }
  };

  writeReturn = () => {
    const label = "__VM_RETURN";
    if (this.existingLabels[label]) {
      this.writeLines(
        formatASM(`
          @${label}
          0;JMP
        `),
        "return"
      );
      return;
    }
    this.existingLabels[label] = true;

    const setSymbol = (symbol: string) => `
        @R13
        AM=M-1
        D=M
        @${symbol}
        M=D
    `;

    this.writeLines(
      formatASM(`
        (${label})
        // FRAME = LCL
        @LCL
        D=M
        @R13
        M=D

        // RET = *(FRAME-5)
        @5
        A=D-A
        D=M
        @R14
        M=D

        // *ARG = pop()
        @SP
        AM=M-1
        D=M
        @ARG
        A=M
        M=D

        // SP = ARG+1
        @ARG
        D=M
        @SP
        M=D+1

        // THAT = *(FRAME-1)
        ${setSymbol("THAT")}
        
        // THIS = *(FRAME-2)
        ${setSymbol("THIS")}

        // ARG = *(FRAME-3)
        ${setSymbol("ARG")}

        // LCL = *(FRAME-4)
        ${setSymbol("LCL")}

        // goto RET
        @R14
        A=M
        0;JMP
      `),
      "return"
    );
  };

  writeCall = (funcName: string, numArgs: number) => {
    const pushD = () =>
      formatASM(`
        @SP // increment stack pointer
        M=M+1
        A=M-1 // set stack pointer - 1 to D
        M=D
      `);

    const returnAddressSymbol = `${this.fileName}:${this.returnAddressIndex++}`;
    this.writeLines(
      formatASM(`
        // push return-address onto stack
        @${returnAddressSymbol}
        D=A
        ${pushD()}
      `),
      "call"
    );

    const label = `__VM_CALL:${numArgs}`;
    if (!this.existingLabels[label]) {
      this.writeLines(
        formatASM(`
          @${funcName}
          D=A
          @R15
          M=D
          (${label})
          // push LCL
          @LCL
          D=M
          ${pushD()}

          // push ARG
          @ARG
          D=M
          ${pushD()}

          // push THIS
          @THIS
          D=M
          ${pushD()}

          // push THAT
          @THAT
          D=M
          ${pushD()}

          // ARG = SP-n-5
          @SP
          D=M
          @${numArgs + 5}
          D=D-A
          @ARG
          M=D

          // LCL = SP
          @SP
          D=M
          @LCL
          M=D

          @R15
          A=M
          0;JMP
        `),
        "call"
      );
      this.existingLabels[label] = true;
    } else {
      this.writeLines(
        formatASM(`
          @${funcName}
          D=A
          @R15
          M=D
          @${label}
          0;JMP
        `),
        "call"
      );
    }

    this.writeLines(
      formatASM(`
        // goto f
        @${funcName}
        0;JMP
        // (return-address)
        (${returnAddressSymbol})
      `),
      "call"
    );
  };

  public translate = () => {
    while (this.parser.hasMoreCommands()) {
      this.parser.advance();
      const cType = this.parser.commandType;
      if (cType === CType.PUSH) {
        this.writeLines(
          `// push ${this.parser.arg1} ${this.parser.arg2}`,
          "comment"
        );
        this.indent();
        const segment = this.parser.arg1;
        if (!isSegment(segment)) {
          throw new Error(`Invalid segment ${segment}`);
        }
        if (this.parser.arg2 === undefined) {
          throw new Error(`push command must specify index of segment`);
        }
        this.writePush(segment, this.parser.arg2);
        this.dedent();
      }
      if (cType === CType.POP) {
        this.writeLines(
          `// pop ${this.parser.arg1} ${this.parser.arg2}`,
          "comment"
        );
        this.indent();
        const segment = this.parser.arg1;
        if (!isSegment(segment)) {
          throw new Error(`Invalid segment ${segment}`);
        }
        if (this.parser.arg2 === undefined) {
          throw new Error(`pop command must specify index of segment`);
        }
        this.writePop(segment, this.parser.arg2);
        this.dedent();
      }
      if (cType === CType.ARITHMETIC) {
        this.writeLines(`// ${this.parser.arg1}`, "comment");
        this.indent();
        const command = this.parser.arg1;
        if (!isArithmetic(command)) {
          throw new Error(`Unexpected arithmetic command ${command}`);
        }
        this.writeArithmetic(command);
        this.dedent();
      }
      if (cType === CType.LABEL) {
        this.writeLines(`// label ${this.parser.arg1}`, "comment");
        this.indent();
        if (this.parser.arg1 === undefined) {
          throw new Error("label command must have an argument");
        }
        this.writeLabel(this.parser.arg1);
        this.dedent();
      }
      if (cType === CType.IF) {
        this.writeLines(`// if-goto ${this.parser.arg1}`, "comment");
        this.indent();
        if (this.parser.arg1 === undefined) {
          throw new Error("if-goto command must have an argument");
        }
        this.writeIf(this.parser.arg1);
        this.dedent();
      }
      if (cType === CType.GOTO) {
        this.writeLines(`// goto ${this.parser.arg1}`, "comment");
        this.indent();
        if (this.parser.arg1 === undefined) {
          throw new Error("goto command must have an argument");
        }
        this.writeGoto(this.parser.arg1);
        this.dedent();
      }
      if (cType === CType.FUNCTION) {
        this.writeLines(
          `// function ${this.parser.arg1} ${this.parser.arg2}`,
          "comment"
        );
        this.indent();
        if (this.parser.arg1 === undefined || this.parser.arg2 === undefined) {
          throw new Error("function command takes two arguments");
        }
        this.writeFunction(this.parser.arg1, this.parser.arg2);
        this.dedent();
      }
      if (cType === CType.RETURN) {
        this.writeLines(`// return`, "comment");
        this.indent();
        this.writeReturn();
        this.dedent();
      }
      if (cType === CType.CALL) {
        this.writeLines(
          `// call ${this.parser.arg1} ${this.parser.arg2}`,
          "comment"
        );
        this.indent();
        if (this.parser.arg1 === undefined || this.parser.arg2 === undefined) {
          throw new Error("call command takes two arguments");
        }
        this.writeCall(this.parser.arg1, this.parser.arg2);
        this.dedent();
      }
      this.output += "\n";
    }
    return this.output;
  };
}
