import RemoteFS from "./RemoteFS";
import type { FetchState } from "./RemoteFS";

import {
  assembleHackASM,
  compileJackToVM,
  translateVMFilesToASM,
} from "./tools";
import type { File } from "./tools";

export function getFilename(url: string) {
  const parts = url.split("/");
  return parts[parts.length - 1];
}

export type CompilationOutput = {
  jackFiles: File[];
  vmFiles: File[];
  asmCode: string;
  machineCode: string;
  error?: Error;
};

export type CompilationTargetType = "vm" | "asm" | "hack";

export default async function compileUrls(
  urlsToCompile: string[],
  config?: { downTo: CompilationTargetType }
) {
  const loadFiles = async (extension: string) => {
    const urlsToLoad = urlsToCompile.filter((url) => url.endsWith(extension));
    const loadedFiles = await RemoteFS.get().getFiles(urlsToLoad);
    return loadedFiles.map((file: FetchState) => ({
      filename: getFilename(file.url),
      text: file.data,
    }));
  };

  const { downTo = "hack" } = config || {};

  const output: CompilationOutput = {
    jackFiles: await loadFiles(".jack"),
    vmFiles: [],
    asmCode: "",
    machineCode: "",
  };

  try {
    output.vmFiles = [
      ...compileJackToVM(output.jackFiles),
      ...(await loadFiles(".vm")),
    ];
  } catch (e) {
    output.error = e;
    return output;
  }
  if (downTo === "vm") {
    return output;
  }

  try {
    output.asmCode = translateVMFilesToASM(output.vmFiles);
  } catch (e) {
    output.error = e;
    return output;
  }

  if (downTo === "asm") {
    return output;
  }

  try {
    output.machineCode = assembleHackASM(output.asmCode);
  } catch (e) {
    output.error = e;
    return output;
  }

  return output;
}
