/// <reference types="emscripten" />
import { CompilationOutput } from "./compileUrls";
import createHackModule from "./hack";
import RustHackMachine from "./RustHackMachine";

/// Extending Module
// This requires -s "EXTRA_EXPORTED_RUNTIME_METHODS=['cwrap','getValue','setvalue']"
// in emcc compilation flags
interface MyEmscriptenModule extends EmscriptenModule {
  cwrap: typeof cwrap;
  getValue: typeof getValue;
  setValue: typeof setValue;
}

type HackModule = {
  module: MyEmscriptenModule;
  loadProgram: (program: string) => number;
  tick: (pointer: number, numSteps: number) => number;
  reset: (pointer: number) => void;
  renderScreen: (pointer: number) => number;
  freeScreen: (pointer: number) => void;
};

let __hackModule: HackModule;
let __hackModulePromise: Promise<MyEmscriptenModule>;

const getHack = async (): Promise<HackModule> => {
  if (!__hackModule) {
    if (!__hackModulePromise) {
      __hackModulePromise = createHackModule({
        locateFile: (path: string, scriptDirectory: string) => {
          return path;
        },
      });
    }
    const module = await __hackModulePromise;
    if (!__hackModule) {
      __hackModule = {
        module,
        loadProgram: module.cwrap("load_program", "number", ["string"]),
        tick: module.cwrap("tick", "number", ["number", "number"]),
        reset: module.cwrap("reset", null, ["number"]),
        renderScreen: module.cwrap("render_screen", "number", ["number"]),
        freeScreen: module.cwrap("free_screen", null, ["number"]),
      };
    }
  }
  return __hackModule;
};

export interface IHackMachine {
  numCycles: number;
  tick(n: number): void;
  reset(): void;
  setKeyboard(event: { key: string } | null): void;
  drawScreen(ctx: CanvasRenderingContext2D): void;
}

export function getKeyValue(key: string) {
  const keyMap: Record<string, number> = {
    ArrowLeft: 130,
    ArrowUp: 131,
    ArrowRight: 132,
    ArrowDown: 133,
  };
  let value = 0;
  value = keyMap[key];
  if (value === undefined) {
    value = key.charCodeAt(0);
  }
  return value;
}

export class CHackMachine implements IHackMachine {
  private _p: number;
  private wasm: HackModule;
  public numCycles: number = 0;
  private constructor(wasm: HackModule, program: string) {
    this.wasm = wasm;
    this._p = this.wasm.loadProgram(program);
  }

  static async create(program: string) {
    const hack = await getHack();
    return new CHackMachine(hack, program);
  }

  tick(n: number) {
    n = n || 1;
    const pc = this.wasm.tick(this._p, n);
    if (pc === -1) {
      throw new Error("Failed to tick");
    }
    this.numCycles += n;
    return pc;
  }

  reset() {
    this.wasm.reset(this._p);
    this.numCycles = 0;
  }

  getRegisters() {
    return {
      PC: this.wasm.module.getValue(this._p, "i32"),
      A: this.wasm.module.getValue(this._p + 4, "i32"),
      D: this.wasm.module.getValue(this._p + 8, "i32"),
    };
  }

  dump(start: number, end: number) {
    start = start || 0;
    end = end || start + 3;

    const state = this.getRegisters();
    let s = `PC=${state.PC} A=${state.A} D=${state.D}`;
    if (end > 0) {
      s += ` RAM[${start}..${end}]=[`;
      for (let i = start; i < end; i++) {
        s += this.wasm.module.getValue(this._p + 3 * 4 + i * 4, "i16");
        if (i < end - 1) {
          s += " ";
        }
      }
      s += "]";
    }
    return s;
  }

  setRAM(offset: number, value: number) {
    this.wasm.module.setValue(this._p + 3 * 4 + offset * 4, value, "i16");
  }

  getRAM(offset: number) {
    return this.wasm.module.getValue(this._p + 3 * 4 + offset * 4, "i16");
  }

  setKeyboard(event: { key: string } | null) {
    this.setRAM(24576, event ? getKeyValue(event.key) : 0);
  }

  drawScreen(ctx: CanvasRenderingContext2D) {
    const totalPixels = 256 * 512;

    const screenPointer = this.wasm.renderScreen(this._p);
    let adata = new Uint8ClampedArray(
      this.wasm.module.HEAPU8.buffer,
      screenPointer,
      totalPixels * 4
    );
    ctx.putImageData(new ImageData(adata, 512, 256), 0, 0);
  }
}

export type MachineVariant = "RustVM" | "Rust" | "C";

export async function createHackMachine(
  variant: "C" | "Rust" | "RustVM",
  program: CompilationOutput
): Promise<IHackMachine> {
  if (variant === "C") {
    if (program.error) {
      throw program.error;
    }
    return CHackMachine.create(program.machineCode);
  }
  return RustHackMachine.create(program, variant);
}
