import type { Arithmetic, Segment } from "./VMTranslator";

type WriteableOutput = {
  writeLn(s: string): void;
};

export default class VMWriter {
  output: WriteableOutput;
  indentation: string = "";

  constructor(output: WriteableOutput) {
    this.output = output;
  }

  indent() {
    this.indentation += "  ";
  }
  dedent() {
    this.indentation = this.indentation.slice(0, -2);
  }

  private writeLn(s: string) {
    // seems like the vm emulator doesn't like
    // indented vmcode...
    // this.output.writeLn(this.indentation + s);
    this.output.writeLn(s);
  }

  writePush(segment: Segment, index: number) {
    this.writeLn(`push ${segment} ${index}`);
  }
  writePop(segment: Segment, index: number) {
    this.writeLn(`pop ${segment} ${index}`);
  }
  writeArithmetic(command: Arithmetic) {
    this.writeLn(command);
  }
  writeLabel(label: string) {
    this.writeLn(`label ${label}`);
  }
  writeGoto(label: string) {
    this.writeLn(`goto ${label}`);
  }
  writeIf(label: string) {
    this.writeLn(`if-goto ${label}`);
  }
  writeCall(name: string, numArgs: number) {
    this.writeLn(`call ${name} ${numArgs}`);
  }
  writeFunction(name: string, numLocals: number) {
    this.writeLn("");
    this.writeLn(`function ${name} ${numLocals}`);
  }
  writeReturn() {
    this.writeLn(`return`);
  }
  writeComment(comment: string) {
    this.writeLn(`// ${comment}`);
  }
}
