export enum CommandType {
  A_COMMAND,
  C_COMMAND,
  L_COMMAND,
}

export type ACommand = {
  commandType: CommandType.A_COMMAND;
  symbol: string;
};
export type LCommand = {
  commandType: CommandType.L_COMMAND;
  symbol: string;
};
export type CCommand = {
  commandType: CommandType.C_COMMAND;
  dest: string;
  comp: string;
  jump: string;
};

export type Command = ACommand | LCommand | CCommand;

export default class ASMParser {
  asm: string;
  private charIndex = 0;
  constructor(asm: string) {
    this.asm = asm;
  }

  /**
   * Returns the next non-empty line of assembly. Could still be empty spaces.
   * When there are no more lines left, it will return an empty string.
   */
  getNextLine() {
    let line = "";
    while (line.length === 0 && this.charIndex < this.asm.length) {
      let endIndex = this.asm.indexOf("\n", this.charIndex);
      if (endIndex === -1) {
        endIndex = this.asm.length;
      }
      line = this.asm.slice(
        this.charIndex,
        // handle windows CRLF line endings... ugg.
        this.asm[endIndex - 1] === "\r" ? endIndex - 1 : endIndex
      );
      this.charIndex = endIndex + 1;
    }
    return line;
  }

  getNextToken() {
    let token = "";
    let line: string;
    while (token.length === 0 && (line = this.getNextLine())) {
      // eat leading whitespace
      let i = 0;
      while (line[i] === " ") {
        i++;
      }

      // eat token
      let j = i;
      while (j < line.length && line[j] !== "/" && line[j] !== " ") {
        j++;
      }
      token = line.slice(i, j);
    }
    return token;
  }

  advance = (): Command | null => {
    const command = this.getNextToken();

    if (!command) {
      return null;
    }

    // handle A_COMMAND
    if (command[0] === "@") {
      return {
        commandType: CommandType.A_COMMAND,
        symbol: command.slice(1),
      };
    }

    // handle L_COMMAND
    if (command[0] === "(") {
      return {
        commandType: CommandType.L_COMMAND,
        symbol: command.slice(1, command.length - 1),
      };
    }

    // handle C_COMMAND
    const commandType = CommandType.C_COMMAND;
    let dest = "";
    let comp = "";
    let jump = "";

    let i = 0;
    let chunk = "";
    while (command[i] !== ";" && command[i] !== "=") {
      chunk += command[i++];
    }
    if (command[i] === "=") {
      dest = chunk;
      comp = command.slice(i + 1);
    } else {
      comp = chunk;
    }

    if (command[i] === ";") {
      jump = command.slice(i + 1);
    }
    return {
      commandType,
      dest,
      comp,
      jump,
    };
  };
}
