import type { IHackMachine } from "./HackMachine";
import type { WebMachine, WebVM } from "hack";
import { getKeyValue } from "./HackMachine";
import type { CompilationOutput } from "./compileUrls";

export default class RustHackMachine implements IHackMachine {
  static async create(
    program: CompilationOutput,
    variant: "RustVM" | "Rust"
  ): Promise<IHackMachine> {
    const hack = await import("hack");
    hack.init_panic_hook();
    let machine;
    if (variant === "Rust") {
      machine = hack.WebMachine.new();
      if (program.asmCode) {
        machine.load_asm_program(program.asmCode);
      } else {
        machine.load_program(program.machineCode);
      }
    } else {
      machine = hack.WebVM.new();
      for (let file of program.vmFiles) {
        machine.load_file(file.filename, file.text);
      }
      machine.init();
    }

    return new RustHackMachine(machine, program);
  }

  private m: WebMachine | WebVM;
  private constructor(m: WebMachine | WebVM, program: CompilationOutput) {
    this.m = m;
  }

  numCycles: number = 0;
  tick(n: number): void {
    this.m.tick(n);
    this.numCycles += n;
  }
  reset(): void {
    this.m.reset();
    this.numCycles = 0;
  }
  setKeyboard(event: { key: string } | null): void {
    this.m.set_keyboard(event ? getKeyValue(event.key) : 0);
  }
  drawScreen(ctx: CanvasRenderingContext2D): void {
    this.m.draw_screen(ctx);
  }
}
