import { useEffect, useState, useRef, useCallback } from "react";
import compileUrls from "./compileUrls";
import type { CompilationTargetType } from "./compileUrls";
import { createHackMachine } from "./HackMachine";
import type { IHackMachine, MachineVariant } from "./HackMachine";

const loadDoc = async (url: string): Promise<string> => {
  const res = await fetch(url);
  if (res.ok) {
    const text = await res.text();
    return text;
  } else {
    throw new Error("Unable to load Pong.hack");
  }
};

export default function useHackMachine(
  url: string | string[],
  {
    speed,
    onTick,
    variant = "Rust",
    paused: initPaused = true,
  }: {
    speed: number;
    onTick?: (machine: IHackMachine, elapsedTimeMs: number) => void;
    variant?: "Rust" | "C" | "RustVM";
    paused?: boolean;
  }
) {
  const [loading, setLoading] = useState(false);
  const [machine, setMachine] = useState<IHackMachine>();
  const [paused, setPaused] = useState(initPaused);

  const canvasRef = useRef<HTMLCanvasElement>(null);

  useEffect(() => {
    setLoading(true);
    const context = canvasRef.current?.getContext("2d");
    context &&
      context.clearRect(0, 0, context.canvas.width, context.canvas.height);
    if (typeof url === "string") {
      loadDoc(url).then(async (text) => {
        setMachine(
          await createHackMachine(variant, {
            jackFiles: [],
            vmFiles: [],
            asmCode: "",
            machineCode: text,
          })
        );
        setLoading(false);
      });
    } else if (url.length > 0) {
      const targetMap: Record<MachineVariant, CompilationTargetType> = {
        C: "hack",
        Rust: "asm",
        RustVM: "vm",
      };
      compileUrls(url, { downTo: targetMap[variant] }).then(async (output) => {
        setMachine(await createHackMachine(variant, output));
        setLoading(false);
      });
    }
  }, [url, variant]);

  useEffect(() => {
    if (!machine) return;
    if (paused) return;
    const startTime = new Date().getTime();
    const computeInterval = setInterval(() => {
      machine?.tick(speed);
      if (onTick) {
        onTick(machine, new Date().getTime() - startTime);
      }
    }, 0);
    return () => {
      clearInterval(computeInterval);
    };
  }, [machine, paused, onTick, speed]);

  useEffect(() => {
    if (paused) return;
    if (!machine) return;
    const context = canvasRef.current?.getContext("2d");
    if (!context) return;
    const renderInterval = setInterval(() => {
      machine.drawScreen(context);
    }, 1000 / 30);
    return () => {
      clearInterval(renderInterval);
    };
  }, [paused, machine]);

  const onKeyDown = useCallback(
    (event: KeyboardEvent) => {
      event.preventDefault();
      event.stopPropagation();
      event.stopImmediatePropagation();
      machine?.setKeyboard(event);
    },
    [machine]
  );
  const onKeyUp = useCallback(() => machine?.setKeyboard(null), [machine]);

  useEffect(() => {
    if (paused) return;
    window.addEventListener("keydown", onKeyDown);
    window.addEventListener("keyup", onKeyUp);
    return () => {
      window.removeEventListener("keydown", onKeyDown);
      window.removeEventListener("keyup", onKeyUp);
    };
  }, [paused, onKeyDown, onKeyUp]);

  const reset = () => {
    if (!machine) return;
    machine.reset();
    const context = canvasRef.current?.getContext("2d");
    context && machine.drawScreen(context);
    onTick && onTick(machine, 0);
  };

  return {
    loading,
    canvasRef,
    paused,
    setPaused,
    reset,
  };
}
