export type SymbolKind = "static" | "field" | "arg" | "var" | "none";
export type SymbolTableItem = {
  name: string;
  kind: SymbolKind;
  type: string;
  index: number;
};
export default class SymbolTable {
  classScope: Record<string, SymbolTableItem> = {};
  subroutineScope: Record<string, SymbolTableItem> = {};
  kindIndex: Record<SymbolKind, number> = {
    static: 0,
    field: 0,
    var: 0,
    arg: 0,
    none: 0,
  };
  addIdentifier(name: string, type: string, kind: SymbolKind) {
    const item = { name, kind, type, index: this.kindIndex[kind]++ };
    if (kind === "static" || kind === "field") {
      this.classScope[name] = item;
    } else {
      this.subroutineScope[name] = item;
    }
    return item;
  }

  startSubroutine() {
    this.subroutineScope = {};
    this.kindIndex.var = 0;
    this.kindIndex.arg = 0;
  }

  private _get(name: string): SymbolTableItem | null {
    return this.subroutineScope[name] || this.classScope[name];
  }

  get(name: string): SymbolTableItem {
    const symbol = this._get(name);
    if (symbol === null) {
      throw new Error(`Could not find "${name}" in symbol table`);
    }
    return symbol;
  }

  getNumFields(): number {
    return Object.values(this.classScope).filter(
      (symbol) => symbol.kind === "field"
    ).length;
  }

  has(name: string): boolean {
    return !!this._get(name);
  }
}
